import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';
import linkState from 'linkstate';

import { route } from 'preact-router';

import mu from '../../lib/musher-util';

const initialState = {
	searchText: '',
    showInfo: {},
};

const MARGIN_TOP_BACK = mu.marginTopBack(false);

@withText({
    public: <Text id='teams.public'>Public visible</Text>,
    publicHelp: <Text id='teams.public-help'>Should it be possible to follow your team? All follow-requests has to be approved by you.</Text>,
    publicWorkouts: <Text id='teams.public-workouts'>Public workouts</Text>,
    publicWorkoutsHelp: <Text id='teams.public-workouts-help'>Should your workouts be visible to your followers?</Text>,
    publicWorkoutDetails: <Text id='teams.public-workouts-details'>Public workouts details</Text>,
    publicWorkoutDetailsHelp: <Text id='teams.public-workouts-details-help'>Should your workouts details be visible to your followers?</Text>,
    publicWorkoutSummary: <Text id='teams.public-workouts-summary'>Public workouts summary</Text>,
    publicWorkoutSummaryHelp: <Text id='teams.public-workouts-summary-help'>Should your workout summary be visible to your followers?</Text>,
    publicTrophys: <Text id='teams.public-trophys'>Public trophys</Text>,
    publicTrophysHelp: <Text id='teams.public-trophys-help'>Should your trophys be visible to your followers?</Text>,
    publicMemories: <Text id='teams.public-memories'>Public memories</Text>,
    publicMemoriesHelp: <Text id='teams.public-memories-help'>Should your memories be visible to your followers?</Text>,
    publicDogs: <Text id='teams.public-dogs'>Public dogs</Text>,
    publicDogsHelp: <Text id='teams.public-dogs-help'>Should your dogs be visible to your followers?</Text>,
})
@observer
class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    loadTeam = async (props = this.props) => {
		const { teamStore, userStore, workoutStore } = props.stores;
        await teamStore.load(props.teamid, false, {
            // addData: ['dogs', 'users', 'followers', 'blockedFollowers'],
        });
    }

	componentDidMount() {
		this.loadTeam();
	}

    toggleInfo = (e) => {
        const { name } = e.target.closest('i').dataset;
        const { showInfo } = this.state;
        showInfo[name] = !showInfo[name];
        this.setState({ showInfo });
	}

    toggleSetting = async (field) => {
        const { teamStore } = this.props.stores;
		const { team } = teamStore;
        const { id } = team;
        const value = team[field] ? 0 : 1;
        teamStore.updateField(id, field, value);
        await teamStore.saveField(id, field, value);
    }

    updateSetting = async (field, value) => {
        const { teamStore } = this.props.stores;
		const { team } = teamStore;
        const { id } = team;
        teamStore.updateField(id, field, value);
        await teamStore.saveField(id, field, value);
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	render() {
		const { teamStore, userStore } = this.props.stores;
        const { teamTypes, teamAmbitions, user } = userStore;
        const { language = 'en' } = user;
        const { team } = teamStore;
        const { searchText, showInfo = {}, settings = {} } = this.state;

		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

				<div class='row m-2'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h1><Text id='settings.team'>Team</Text></h1>
                    </div>
                </div>

				<div class='row m-2'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2'>
						<h5 class='m-0 text-muted'>{team.name}</h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3 bg-light rounded-lg' style='font-size: 1.2em;'>
                        {['public'].map((setting, idx) => <>
                            <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1' style='line-height: 1.0em;'>
                                        {this.props[setting] || setting}
                                        {this.props[`${setting}Help`] && <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={setting} />}
                                        {this.props[`${setting}Help`] && showInfo[setting] && <>
                                            <br /><small class='text-muted'>{this.props[`${setting}Help`]}</small>
                                        </>}
                                    </span>
                                    <div class='custom-control custom-switch custom-switch-lg mt-2 ml-3' style="transform: scale(1.8);">
                                        <input type='checkbox' class='custom-control-input' id={`settings${setting}`} onInput={() => this.toggleSetting(setting)} checked={team[setting]} />
                                        <label class='custom-control-label' for={`settings${setting}`} />
                                    </div>
                                </div>
                            </div>
                        </>)}
					</div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2' style={`opacity: ${team.public ? 1.0 : 0.2};`}>
						<h5 class='m-0 text-muted'><Text id='teams.public-settings'>Public settings</Text></h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3 bg-light rounded-lg' style={`font-size: 1.2em; opacity: ${team.public ? 1.0 : 0.2};`}>
                        {['publicWorkouts', 'publicWorkoutSummary', 'publicWorkoutDetails', 'publicTrophys', 'publicMemories', 'publicDogs'].map((setting, idx) => <>
                            <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1' style='line-height: 1.0em;'>
                                        {this.props[setting] || setting}
                                        {this.props[`${setting}Help`] && <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={setting} />}
                                        {this.props[`${setting}Help`] && showInfo[setting] && <>
                                            <br /><small class='text-muted'>{this.props[`${setting}Help`]}</small>
                                        </>}
                                    </span>
                                    <div class='custom-control custom-switch custom-switch-lg mt-2 ml-3' style="transform: scale(1.8);">
                                        <input type='checkbox' class='custom-control-input' id={`settings${setting}`} onInput={() => this.toggleSetting(setting)} checked={team[setting]} />
                                        <label class='custom-control-label' for={`settings${setting}`} />
                                    </div>
                                </div>
                            </div>
                        </>)}
					</div>
                    


                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2'>
						<h5 class='m-0 text-muted'><Text id='teams.type'>Type</Text></h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-0 bg-light rounded-lg' style='font-size: 1.2em;'>
                        {teamTypes && teamTypes.map((type, idx) => <>
                            <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1'>
                                        <Link className='stretched-link' activeClassName={'active'}
                                            data-value={type.id}
                                            onClick={() => this.updateSetting('type', type.id)}
                                        >
                                            {language === 'no' ? type.name : (type.lang[language] || type.name)}
                                        </Link>
                                        {/* <xmp>{JSON.stringify(type, null, 4)}</xmp> */}
                                    </span>
                                    {team.type === type.id && <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                        <i class='fas fa-check text-primary' />
                                    </button>}
                                </div>
                            </div>
                        </>)}
					</div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3'>
                        <small class='text-muted'><Text id='teams.type-help'>Type team</Text></small>
                    </div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5 mb-2'>
						<h5 class='m-0 text-muted'><Text id='teams.ambitions'>Ambisjoner</Text></h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-0 bg-light rounded-lg' style='font-size: 1.2em;'>
                        {teamAmbitions && teamAmbitions.map((type, idx) => <>
                            <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1'>
                                        <Link className='stretched-link' activeClassName={'active'}
                                            data-value={type.id}
                                            onClick={() => this.updateSetting('ambitions', type.id)}
                                        >
                                            {language === 'no' ? type.name : (type.lang[language] || type.name)}
                                        </Link>
                                        {/* <xmp>{JSON.stringify(type, null, 4)}</xmp> */}
                                    </span>
                                    {team.ambitions === type.id && <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                        <i class='fas fa-check text-primary' />
                                    </button>}
                                </div>
                            </div>
                        </>)}
					</div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3'>
                        <small class='text-muted'><Text id='teams.ambitions-help'>Ambisjonsniv√• til teamet.</Text></small>
                    </div>

                </div>

			</div>
		);
	}
}

export default Settings;
